import api from "../../config/api.js"
import Util from "../../utils/utils"

export default {
  name: 'record',
  data () {
    return {
      token: '',
      roomId: '',
      num: 0,
      owner: 0,
      points: 0,
      recordlist: null
    }
  },
  created() {
  },
  mounted() {
    this.getRank()
  },
  methods: {
    getRank() {
      api.Axios.get(api.RECORD).then(res => {
        console.log('res', res);
        if (res.data.code === 200) {
          this.recordlist = res.data.data.aokePowerRecords
        } else {
          this.$toast(res.data.msg, 1500)
        }
      })
    }
  }
}



